import axios from 'axios';

export type Logs = {
    login_time: Date;
    logout_time: Date | null;
    student: {
        first_name: string;
        last_name: string;
        middle_name: string;
        section: string;
        stud_number: string;
    }   
}

export async function GetAttendanceLogs() {
    const response = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/logs`, { headers: { 'Content-Type': 'application/json' } });
    const attendaceLog: Logs[] = response.data;

    return attendaceLog;
}