import React, { useState, useEffect } from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
// Icons
import GoogleIcon from '@mui/icons-material/Google';
// Utils
import { useFirebaseApp } from "../utils/FirebaseProvider";
import Page from '../components/Page';
import { useNavigate } from "react-router-dom";

function Copyright(props: any) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright © '}
      <Link color="inherit" href="#">
        TraceTemp
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

type Credentials = {
  email: string;
  password: string;
}

export default function Login() {
  const navigate = useNavigate();
  const { currentUser, loginUser, signinWithGoogle } = useFirebaseApp();
  const [credentials, setCredentials] = useState<Credentials>({
    email: '',
    password: ''
  });
  const [error, setError] = useState<string | null>(null);

  const { email, password } = credentials;

  useEffect(() => {
    if (currentUser !== null) {
      return navigate("/dashboard/app");
    }
  }, [currentUser, navigate])

  const handleTextChange = (event: React.ChangeEvent<HTMLInputElement>) => setCredentials({ ...credentials, [event.target.name]: event.target.value });

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    
    if (loginUser !== undefined) {
      loginUser(email, password).then(result => {
        return navigate("/dashboard/app");
      }).catch(error => setError((error as Error).message));
    }
  };

  const googleLogin = (event: React.MouseEvent<HTMLButtonElement>) => {
    if (signinWithGoogle !== undefined) {
      signinWithGoogle().then((result) => {
        return navigate("/dashboard/app");
      }).catch((error) => {
        setError((error as Error).message)
      })
    }
  }

  const messageConverter = (error: string | null) => {
    if (error === "Firebase: Error (auth/wrong-password).") return "Incorrect password."
    else if (error === "Firebase: Error (auth/user-not-found).") return "Account not found."
    else return error;
  }

  return (
      <Container component="main" maxWidth="xs">
        <Page title='Sign in' />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Admin Sign in
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
              value={email}
              onChange={handleTextChange}
              autoFocus
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              value={password}
              onChange={handleTextChange}
              autoComplete="current-password"
              error={Boolean(error !== null)}
              helperText={messageConverter(error)}
            />
            <FormControlLabel
              control={<Checkbox value="remember" color="primary" />}
              label="Remember me"
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Sign In
            </Button>
            <Button
              color="inherit"
              startIcon={<GoogleIcon color="error" />}
              fullWidth
              variant="contained"
              onClick={googleLogin}
              sx={{ mb: 3 }}
            >
              Sign In With Google
            </Button>
            <Grid container>
              <Grid item xs>
                <Link href="/reset" variant="body2">
                  Forgot password?
                </Link>
              </Grid>
              <Grid item>
              </Grid>
            </Grid>
          </Box>
        </Box>
        <Copyright sx={{ mt: 8, mb: 4 }} />
      </Container>
  );
}