import React, { useEffect, useState } from 'react';
import { useNavigate } from "react-router-dom";
import { io } from "socket.io-client";
//components
import Page from '../components/Page';
import Label from '../components/Label';
import StudentListToolbar from "../components/StudentToolbar";
import StudentListHead from "../components/StudentListHead";
import RegisterStudent from "../components/RegisterStudent";
//mui material \
import Box from "@mui/material/Box";
import Container from "@mui/material/Container";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import Card from "@mui/material/Card";
import TableContainer from "@mui/material/TableContainer";
import Table from "@mui/material/Table";
import TableRow from "@mui/material/TableRow";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TablePagination from "@mui/material/TablePagination";
import Avatar from "@mui/material/Avatar";
import useMediaQuery from '@mui/material/useMediaQuery';
//icons
import AddIcon from '@mui/icons-material/Add';
//utils
import { capitalCase } from 'change-case';
import { GetAttendanceLogs, Logs } from "../utils/GetRequests";
import { useFirebaseApp } from "../utils/FirebaseProvider";

const headLabels = [
  {
    id: 'name',
    label: 'Name',
    alignRight: false
  },
  {
    id: 'studNum',
    label: 'Student No.',
    alignRight: false
  },
  {
    id: 'section',
    label: 'Section',
    alignRight: false
  },
  {
    id: 'date',
    label: 'Date',
    alignRight: false
  },
  {
    id: 'login',
    label: 'Login',
    alignRight: true
  },
  {
    id: 'logout',
    label: 'Logout',
    alignRight: true
  }
];

const socket = io(process.env.REACT_APP_BACKEND_URL as string);

function Dashboard() {
  const matches = useMediaQuery('(min-width:600px)');
  const { currentUser } = useFirebaseApp();
  const navigate = useNavigate();
  const [attendaceLog, setAttendaceLog] = useState<Logs[]>([]);
  const [filteredLogs, setFilteredLogs] = useState<Logs[]>([]);
  const [rowsPerPage, setRowsPerPage] = useState<number>(5);
  const [page, setPage] = useState<number>(0);
  const [open, setOpen] = useState<boolean>(false);
  const [rfid, setRfid] = useState<string>("");
  const [filterName, setFilterName] = useState<string>("");

  useEffect(() => {
    if (!currentUser) {
      return navigate("/signin");
    }
  }, [currentUser, navigate])

  useEffect(() => {
    socket.emit("join_room", "common");
  }, []);

  useEffect(() => {
    socket.on("not_found", (data: string) => {
      setRfid(state => data);
      setOpen(state => true);
      window.localStorage.setItem("serial", data);
    })
  }, []);

  useEffect(() => {
    const getLogs = async () => {
      try {
        const logs = await GetAttendanceLogs();
        setAttendaceLog(state => logs);
      } catch (error) {
        console.log(error);
      }
    }
    
    socket.on("update_list", () => {
      getLogs();
    })
  }, []);

  useEffect(() => {
    const getLogs = async () => {
      try {
        const logs = await GetAttendanceLogs();
        setAttendaceLog(state => logs);
      } catch (error) {
        console.log(error);
      }
    }

    getLogs();
  }, []);

  useEffect(() => {
    setFilteredLogs(state => attendaceLog.filter(log => log.student.stud_number.includes(filterName)).slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage));
  }, [rowsPerPage, page, attendaceLog, filterName]);

  const handlePageChange  = (event: React.MouseEvent<HTMLButtonElement, MouseEvent> | null, page: number) => setPage(page);

  const handleRowPerPageChange = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  }

  const openDialog = (event: React.MouseEvent<HTMLButtonElement>) => {
    if (window.localStorage.getItem("serial") !== null) {
      setRfid(window.localStorage.getItem("serial") as string);
      setOpen(true);
    }
  }

  return (
      <>
        <Page title='Dashboard' />
        <Container>
            <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
              <Typography variant={matches ? "h4" : "h6"} color="primary" gutterBottom>
                  Attendance Log
              </Typography>
              {matches && (
                <Button variant="contained" size="medium" onClick={openDialog} startIcon={<AddIcon />}>
                  New Student
                </Button>
              )}
            </Stack>

            <Card sx={{ p: 2 }}>
              <StudentListToolbar filterName={filterName} onFilterName={(event) => setFilterName(event.target.value)} />
              <Box sx={{ overflowX: 'auto' }}>
                <TableContainer sx={{ minWidth: 800 }}>
                  <Table>
                    <StudentListHead orderBy='name' order='asc' headLabel={headLabels} onRequestSort={(property) => {}} />
                    <TableBody>
                      {filteredLogs.map(log => (
                        <TableRow
                          hover
                          key={new Date(log.login_time).toISOString()}
                          tabIndex={-1}
                          role="checkbox"
                        >
                          <TableCell component="th" scope="row">
                            <Stack direction="row" alignItems="center" spacing={2}>
                              <Avatar>NA</Avatar>
                              <Typography variant="subtitle2" noWrap>
                                {capitalCase(log.student.last_name) + ", " + capitalCase(log.student.first_name) + " " + capitalCase(log.student.middle_name.charAt(0)) + "."}
                              </Typography>
                            </Stack>
                          </TableCell>
                          <TableCell align="left">{log.student.stud_number}</TableCell>
                          <TableCell align="left">{log.student.section}</TableCell>
                          <TableCell align="left">{new Date(log.login_time).toDateString()}</TableCell>
                          <TableCell align="right">
                            {new Date(log.login_time).toLocaleString('en-GB', { timeZone: 'UTC' }).split(",")[1]}
                          </TableCell>
                          <TableCell align="right">
                            {log.logout_time ? 
                              new Date(log.logout_time).toLocaleString('en-GB', { timeZone: 'UTC' }).split(",")[1] : (
                                <Label color='error'>
                                  Not Yet
                                </Label>
                              )}
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
              </Box>
              <TablePagination
                rowsPerPageOptions={[5, 10, 25]}
                component="div"
                count={attendaceLog.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onPageChange={handlePageChange}
                onRowsPerPageChange={handleRowPerPageChange}
              />
            </Card>
        </Container>

        <RegisterStudent open={open} rfid={rfid} handleClose={() => setOpen(false)} />
      </>
  )
}

export default Dashboard