import { createContext, useContext, useState, useEffect } from "react";
import { initializeApp } from "firebase/app";
import { getAuth, User, createUserWithEmailAndPassword, signInWithEmailAndPassword, signOut, sendPasswordResetEmail, GoogleAuthProvider, signInWithPopup, UserCredential } from "firebase/auth";

const firebaseConfig = {
  apiKey: "AIzaSyCR68_-JF_7KjqfHW_Hnop87nT7IB-rA7o",
  authDomain: "tracetemp-399f9.firebaseapp.com",
  projectId: "tracetemp-399f9",
  storageBucket: "tracetemp-399f9.appspot.com",
  messagingSenderId: "1041656578153",
  appId: "1:1041656578153:web:0865af58eff5764089e2f8",
  measurementId: "G-XV5E61FZKT"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const firebaseAuth = getAuth(app);

type FirebaseData = {
    currentUser: User | null,
    signupUser?: (email: string, password: string) => Promise<UserCredential>,
    loginUser?: (email: string, password: string) => Promise<UserCredential>,
    logoutUser?: () => Promise<void>,
    forgetPassword?: (email: string) => Promise<void>,
    signinWithGoogle?: () => Promise<UserCredential>
}

const FirebaseContext = createContext<FirebaseData>({
    currentUser: null
});

export function useFirebaseApp() {
    return useContext(FirebaseContext);
}

export default function FirebaseProvider({ children }: { children: React.ReactNode }) {
    const [currentUser, setCurrUser] = useState<User | null>(null);
    const [loading, setLoading] = useState<boolean>(true);

    useEffect(() => {
        const unsubscribe = firebaseAuth.onAuthStateChanged(user => {
            setCurrUser(state => user);
            setLoading(state => false);
        });

        return unsubscribe;
    }, []);

    const signupUser = (email: string, password: string) => {
        return createUserWithEmailAndPassword(firebaseAuth, email, password);
    }

    const loginUser = (email: string, password: string) => {
        return signInWithEmailAndPassword(firebaseAuth, email, password);
    }

    const logoutUser = () => {
        return signOut(firebaseAuth);
    }

    const forgetPassword = (email: string) => {
        return sendPasswordResetEmail(firebaseAuth, email);
    }

    const signinWithGoogle = () => {
        const provider = new GoogleAuthProvider();
        return signInWithPopup(firebaseAuth, provider);
    }

    const firebaseValue = {
        currentUser,
        signupUser,
        loginUser,
        logoutUser,
        forgetPassword,
        signinWithGoogle
    }

    return (
        <FirebaseContext.Provider value={firebaseValue}>
            {!loading && children}
        </FirebaseContext.Provider>
    );
}



