import { Helmet } from "react-helmet-async";
import { forwardRef } from "react";

type PageProps = {
    title: string,
    meta?: string
}

const Page = forwardRef((props: PageProps, ref) => (
    <Helmet>
        <title>{`${props.title} | TracetTemp`}</title>
        {props.meta}
    </Helmet>
));

export default Page;