import React, { useState } from 'react';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
//utils
import axios from "axios";

type DialogProps = {
    open: boolean;
    handleClose: () => void;
    rfid: string;
}

type StudentRecord = {
    stud_number: string;
    first_name: string;
    last_name: string;
    middle_name: string;
    section: string;
}

function RegisterStudent(props: DialogProps) {
  const { open, rfid, handleClose } = props;
  const [student, setStudent] = useState<StudentRecord>({
    stud_number: '',
    first_name: '',
    last_name: '',
    middle_name: '',
    section: ''
  });
  const { stud_number, first_name, last_name, middle_name, section } = student;

  const handleTextChange = (event: React.ChangeEvent<HTMLInputElement>) => setStudent({ ...student, [event.target.name]: event.target.value });

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    axios.post(`${process.env.REACT_APP_BACKEND_URL}/create`, JSON.stringify({
        stud_number: stud_number,
        first_name: first_name,
        last_name: last_name,
        middle_name: middle_name,
        section: section,
        serial: rfid
    }), { headers: { 'Content-Type': 'application/json' }})
    .then(response => {
        window.localStorage.removeItem("serial");
        handleClose();
    }).catch(error => console.log(error));
  }

  return (
    <Dialog open={open} onClose={handleClose}>
        <form onSubmit={handleSubmit}>
            <DialogTitle>Register Student</DialogTitle>
            <DialogContent>
                <Grid container spacing={2} mt={3}>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            name="serial"
                            label="Serial Number"
                            value={rfid}
                            fullWidth
                            variant="outlined"
                        />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            autoFocus
                            name="first_name"
                            label="First Name"
                            value={first_name}
                            onChange={handleTextChange}
                            fullWidth
                            variant="outlined"
                            required
                        />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            name="middle_name"
                            label="Middle Name"
                            value={middle_name}
                            onChange={handleTextChange}
                            fullWidth
                            variant="outlined"
                            required
                        />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            name="last_name"
                            label="Last Name"
                            value={last_name}
                            onChange={handleTextChange}
                            fullWidth
                            variant="outlined"
                            required
                        />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            name="stud_number"
                            label="Student Number"
                            value={stud_number}
                            onChange={handleTextChange}
                            fullWidth
                            variant="outlined"
                            required
                        />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            name="section"
                            label="Section"
                            value={section}
                            onChange={handleTextChange}
                            fullWidth
                            variant="outlined"
                            required
                        />
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions>
                <Button color="error" onClick={handleClose}>Cancel</Button>
                <Button type="submit">Register</Button>
            </DialogActions>
        </form>
    </Dialog>
  )
}

export default RegisterStudent